"use scrict";
import MyFooter from "@/components/MyFooter/index.vue";
import {toast} from "@/config/package.js";
import {isEmpty} from "@/utils/util.js";

export default {
	name: "MyForm",
	props: {
		value: {
			type: Object,
			default() {
				return {};
			}
		},
		labelWidth: {
			type: Number,
			default: 150
		},
		rules: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	methods: {
		submit: async function () {
			for (let att in this.rules) {
				let item = this.rules[att];
				if (typeof item === "function") {
					let isAuthorized = true;
					item(this.value[att], async message => {
						if(message) {
							isAuthorized = false;
							await toast({title: message, icon: "none"});
						}
					});
					if (!isAuthorized) return;
				} 
				else if (isEmpty(this.value[att])) {
					await toast({title: item, icon: "none"});
					return;
				}
			};
			this.$emit("submit");
		}
	},
	components: {
		MyFooter
	}
}
